.menu {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  display: grid;
  grid-template-rows: auto 1fr;
  z-index: 11;
  -webkit-animation-name: menu-anim;
          animation-name: menu-anim;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
@-webkit-keyframes menu-anim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes menu-anim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
