}
.menu-page:not(:first-child) {
  margin-left: 4vw;
  width: 10vw;
}
.menu-page:nth-child(4),.menu-page:nth-child(5){
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}

